.cuaca {
  width: 100vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 64rem) {
  .cuaca {
    height: 100vh;
  }
}
.cuaca__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 5rem auto;
  width: 90vw;
  height: 100%;
  background-color: var(--warna-card);
  border-radius: 2.5rem;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
}
@media screen and (min-width: 64rem) {
  .cuaca__container {
    border-radius: 5rem;
    max-width: 68.75rem;
    max-height: 43.75rem;
    flex-direction: row;
  }
}
@media screen and (min-width: 87.5rem) {
  .cuaca__container {
    max-width: 81.25rem;
    max-height: 50rem;
  }
}
.cuaca__container__pertama {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  border-radius: unset;
  border-right: unset;
}
@media screen and (min-width: 64rem) {
  .cuaca__container__pertama {
    border-radius: 80px;
    border-right: 1px #96bfce solid;
    flex: 0.4;
  }
}
.cuaca__container__pertama h1 {
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 2px;
  margin-top: 1rem;
  color: var(--warna-dalem);
}
.cuaca__container__pertama p {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 1.25rem;
  color: var(--warna-dalem);
}
@media screen and (min-width: 64rem) {
  .cuaca__container__pertama p {
    margin-bottom: 1rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .cuaca__container__pertama p {
    margin-bottom: 2rem;
  }
}
.cuaca__container__kedua {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
@media screen and (min-width: 64rem) {
  .cuaca__container__kedua {
    flex: 0.6;
  }
}
.cuaca__container__kedua .cuaca__chart {
  display: flex;
  align-items: center;
  width: 100%;
  margin: 2rem 0;
}
@media screen and (min-width: 64rem) {
  .cuaca__container__kedua .cuaca__chart {
    max-width: 43.75rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .cuaca__container__kedua .cuaca__chart {
    width: 100%;
  }
}
.cuaca__container__kedua .cuaca__hari {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 0 1rem;
}
@media screen and (min-width: 64rem) {
  .cuaca__container__kedua .cuaca__hari {
    flex-direction: row;
  }
}
.cuaca__container__kedua .cuaca__hari .card {
  width: 280px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 40px;
  background-color: #9dc8d7;
  transition: 0.5s all ease;
  margin-bottom: 2rem;
}
@media screen and (min-width: 64rem) {
  .cuaca__container__kedua .cuaca__hari .card {
    text-align: center;
    width: 160px;
    height: 230px;
    margin-bottom: 0rem;
    padding: 1rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .cuaca__container__kedua .cuaca__hari .card {
    text-align: center;
    width: 180px;
    height: 250px;
    margin-bottom: 0rem;
    padding: 1rem;
  }
}
.cuaca__container__kedua .cuaca__hari .card img {
  width: 40%;
  margin-top: 1rem;
}
.cuaca__container__kedua .cuaca__hari .card h2 {
  font-size: 1rem;
  margin-top: 2rem;
  color: #eaf0f5;
  font-weight: 400;
  border-bottom: 1px #eaf0f5 solid;
}
.cuaca__container__kedua .cuaca__hari .card p {
  color: #eaf0f5;
  margin-top: 1rem;
  font-size: 1.5rem;
  font-weight: 300;
  transition: 0.5s all ease;
  margin-bottom: 0;
}
@media screen and (min-width: 64rem) {
  .cuaca__container__kedua .cuaca__hari .card p {
    margin-top: 0.5rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .cuaca__container__kedua .cuaca__hari .card p {
    margin-top: 1rem;
  }
}
.cuaca__container__kedua .cuaca__hari .card:hover {
  transform: translateY(-2px);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
}